.tunes-list{margin:3em 0 0;padding-left:0;list-style:none}.tunes-list li{margin-bottom:3em}.tunes-list li:last-child article{margin-bottom:0}

.song{color:#262626;text-align:left;max-width:24em;margin:0 auto 2em;position:relative;box-sizing:border-box;background-color:#fff;border-radius:0.75em;border-bottom-left-radius:0.9em;border-bottom-right-radius:0.9em;border:1px solid black}.song .inside{padding:1.35em 1.75em 1.75em}.song h2{font-size:inherit;line-height:1.5em;margin:0}.song .player{display:flex;align-items:center;flex-direction:row;justify-content:space-between;margin-top:1.5em}.song img{width:5em;border:3px solid #262626;border-radius:0.5em}.song audio{outline:none;max-width:14em;background:#262626}.song audio::-webkit-media-controls-panel,.song audio::-webkit-media-controls{background:#fff}.song .meta{font-size:0.75em;font-weight:800;line-height:1.5;color:rgba(255,255,255,0.9);padding:1.5em 2.25em;background:rgba(0,0,0,0.9);border-bottom-left-radius:1em;border-bottom-right-radius:1em}@media screen and (max-width: 520px){.player{flex-direction:column}}.song-enter-active,.song-exit-active{transition:all 0.2s}.song-enter{opacity:0;transform:scale(0.85)}.song-enter-active{opacity:1;transform:translateX(0)}.song-exit{opacity:1}.song-exit-active{opacity:0;transform:scale(0.85)}


